.blogMain {
	position: relative;
	z-index: 15;
	background: $white; }
.blogBlock {
	padding-top: 50px;
	padding-bottom: 50px;
	&__title {
		font-family: $blogger;
		font-size: $big-font-size;
		font-weight: 700;
		text-align: center;
		line-height: 33px;
		text-transform: uppercase;
		margin: 60px auto;
		color: $purple; } }
.blogItem {
	.btn-product {
		width: 238px;
		margin: 0 auto; } }
.blogCart {
	@include row-flex();
	justify-content: center;
	align-items: flex-start;
	margin-top: 60px;
	margin-bottom: 60px;
	&__img {
		@include col();
		@include size(6);
		@include size-md(12);
		height: 300px;
		border-radius: 10px;
		overflow: hidden;
		@include md(order, 1);
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__text {
		@include col();
		@include size(6);
		@include size-md(12);
		@include md(margin-bottom, 30px); }
	&__link {
		&-heading {
			font-family: $blogger;
			font-size: $big-font-size;
			font-weight: 700;
			text-transform: uppercase;
			line-height: 33px;
			color: $violet;
			margin: 20px 0 0 0;
			@include md(font-size, 24px); } }
	&__descr {
		font-family: $roboto;
		font-size: $base-font-size;
		font-weight: normal;
		line-height: 22px;
		color: $grey;
		margin: 30px 0;
		@include md(font-size, 14px); }
	&__date {
		font-family: $roboto;
		font-size: $base-font-size;
		font-weight: normal;
		line-height: 22px;
		color: $grey;
		margin: 0 0 20px 0;
		@include md(font-size, 14px); }
	&__category {
		a {
			font-family: $roboto;
			font-size: $base-font-size;
			font-weight: normal;
			line-height: 22px;
			color: $green;
			@include md(font-size, 14px); } } }
