.blogContentBlock {
	padding-top: 50px;
	padding-bottom: 50px; }
.blogContent {
	&__heading {
		font-family: $blogger;
		font-size: $big-font-size;
		font-weight: 700;
		line-height: 33px;
		text-transform: uppercase;
		color: $purple;
		text-align: left;
		margin: 30px 0 20px 0;
		@include md(font-size, 24px); }
	p {
		font-family: $roboto;
		font-size: $medium-font-size;
		line-height: 30px;
		color: $grey;
		@include md(font-size, 18px); }
	ul {
		font-size: $medium-font-size;
		color: $grey;
		line-height: 30px; } }
